import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import './bootstrap.min.css';
import * as serviceWorker from './serviceWorker';
import { BrowserRouter, Route } from 'react-router-dom';
const App=React.lazy(() => import('./App'));
const FormPage=React.lazy(() => import('./pages/FormPage'));
const Dashboard = React.lazy(() => import('./pages/Dashboard'));
const RegistrationPage=React.lazy(() => import('./pages/RegistrationPage'));
const UserPage=React.lazy(() => import('./pages/UserPage'));
const AllNotes=React.lazy(() => import('./pages/AllNotes'));
const UserPhotoUploadPage=React.lazy(() => import('./pages/UserPhotoUpload'));

ReactDOM.render(
  <React.StrictMode>
    <React.Suspense fallback={<div>Loading......</div>}>
    <BrowserRouter>
      <switch>
        <Route path='/' component={App} exact/>
        <Route path='/dashboard' component={Dashboard} />
        <Route path='/allnotes' component={AllNotes}/>
        <Route path='/new-notes' component={FormPage}/>
        <Route path='/registration' component={RegistrationPage}/>
        <Route path='/users' component={UserPage}/>
        <Route path='/user-photo-upload' component={UserPhotoUploadPage}/>
      </switch>
    </BrowserRouter>
    </React.Suspense>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
